*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    font-family: 'Source Sans Pro', sans-serif;
    color:#fff;
}

.container{
    width:80%;
    margin:auto;
    padding-top: 20vh;
    
}

.App{
    height:100vh;
    width:100%;
    
}

.title{
    padding:20px 0 40px 0;
    color:#fff;
}



::placeholder, :-ms-input-placeholder, ::-ms-input-placeholder {
  color: #fff;
  opacity: 1;
}

::placeholder {
  color: #fff;
}


.weather-temp{
    padding-bottom:40px;
    padding-left:60px;
}

.weather-temp h1 , .weather-temp h4 , .weather-temp p {
    padding-bottom: 10px;
}

.weather-info p , .weather-info h2, .weather-info h3 {
    text-align:center;
}

.weather-info h2 span {
  font-size: smaller;
}

.user-weather{
    color:#fff;
    padding:30px;
    border-radius: 20px;
    border:2px solid #fff;
}

.navbar{
    background-color:#ddd;
}

.mainImg{
    padding-top:15px;
}

.region{
    margin-top:30px;
}

.regioninput{
    background:none;
    border:none;
    border-bottom:1px solid #fff;
    padding:0 50px 0 0px;
    outline:none;
}

.regioninput::placeholder{
    font-family:'Source Sans Pro', sans-serif;
    font-weight:600;
    font-size:1rem;
    padding-left:0;
}
